<template>
    <div id="js-editor" class="editor" :class="{ 'scroll-v': isHaveScrollingV && focused, 'scroll-h': isHaveScrollingH && focused }" data-lang="js">
        <textarea class="src" ref="src" @focus="focus" @blur="blur" v-model="code" spellcheck="false" @keydown="keydown" @keyup="keyup" @scroll="scroll"></textarea>
        <div class="overlay" ref="overlay">
            <div v-for="(line, i) in code.split('\n')" :key="i" :class="{ focus: i == focusedLine, tl: tabs[i] < tabs[i+1], tg: tabs[i-1] > tabs[i], td: tabs[i] == tabs[i+1], tu: tabs[i] == tabs[i-1] }"></div>
            <div ref="output" class="output" v-html="lines"></div>
        </div>
    </div>
</template>

<script>
import {js} from '../data/lang'

export default {
    name: 'js-editor',
    data () {
    return {
        code: "יהי א = 2;\nא++;\nהפעולה הודעה (מסר) {\n  הקפץ(מסר);\n}\nאם (א < 3) {\n  הודעה('גדול')\n} אחרת אם (א == 3) {\n  הודעה('שווה')\n} אחרת {\n  הודעה('קטן')\n}",
        focusedLine: 0,
        src: null,
        savedReg: null,
        functionsReg: null,
        isHaveScrollingH: false,
        isHaveScrollingV: false,
        focused: false
    }
    },
    mounted: function () {
        this.savedReg = new RegExp('(^|\\s)(' + Object.keys(js.saved).join("|") +
            ')(?=\\(|\\)|\\s|$)', "g");
        this.functionsReg = new RegExp('(^|\\s)(' + Object.keys(js.functions).join("|") +
            ')(?=\\s|\\(|$)', "g");
        this.src = this.$refs.src;
        this.src.onkeyup = this.change;
        this.src.onmouseup = this.change;
    },
    computed: {
      tabs: function () {
        return this.code.split("\n").map((l) => l.length - l.trimLeft().length);
      },
      lines: function () {
        let l = this.code
          .replace(/[=><\+\-]/g, '<g>$&</g>')
          .replace(/(;)(\r|\n|$)/g, '<g>$1</g>$2')
          .replace(this.savedReg, '$1<c>$2</c>')
          .replace(/"[^"\s]*"{0,1}/g, '<s>$&</s>')
          .replace(/'[^'\s]*'{0,1}/g, '<s>$&</s>')
          .replace(/([\u0590-\u05FF_][\u0590-\u05FF0-9_]*[ ]*)\(([^\)\r\n]*)\)/g, '<f>$1</f><b>$2</b>')
          .replace(/\{([^\{]*)\}/g, '<i>$1</i>')
          .replace(/\s+/g, '<e>$&</e>')
          .replace(/[0-9]+/g, '<n>$&</n>')
          .replace(/[\)\(\}\{]/g, '<g>$&</g>')
        return l;
      },
    },
    methods: {
        raw: function(){
            let i = this.code.replace(this.functionsReg, ($0, $1, $2) => $1 + (js.functions[$2] || $2))
                             .replace(this.savedReg, ($0, $1, $2) => $1 + (js.saved[$2] || $2))
            return i
        },
        keydown: function (e) {
          if (e.which != 9) return;
          e.preventDefault();
          let p = this.src.selectionStart,
            l = this.code.substr(0, p);
          this.code = l.substr(0, l.length) + "  " + this.code.substr(p, this.src.length);
          p += 2;
          setTimeout(() => {
            this.src.setSelectionRange(p, p);
          }, 0);
        },
        keyup: function (e) {
          if (e.which !== 13 && e.which != 222 && e.which != 8) return;
          let p = this.src.selectionStart;
          let l = this.code.substr(0, p);
          if (e.which == 13) {
            this.focusedLine = l.split("\n").length - 1;
            if (this.tabs[this.focusedLine] !== 0) return;
            let t = this.tabs[this.focusedLine - 1],
              s = '';
            for (let i = 0; i < t; i++)
              s += ' ';
            this.code = l + s + this.code.substr(p, this.src.length);
            setTimeout(() => {
              this.src.setSelectionRange(p + t, p + t);
            }, 0);
          } else if (e.which == 8) {
            let l = this.code.substr(0, p + 1);
            if (l[l.length - 1] != '"' && l[l.length - 2] != '"') return;
            if (l[l.length - 2] == '"' && l[l.length - 1] == '"') {
              this.code = l.substr(0, l.length - 2) + this.code.substr(p, this.src.length);
              p--;
            } else
              this.code = l.substr(0, l.length - 1) + this.code.substr(p + 1, this.src.length);
            setTimeout(() => {
              this.src.setSelectionRange(p, p);
            }, 0);
          } else {
            this.focusedLine = l.split("\n").length - 1;
            if (l.split('"').length % 2 == 1) return;
            let cl = this.lines[this.focusedLine];
            if (cl.substr(p, cl.length).split('"').length % 2 == 1) {
              this.code = l + '"' + this.code.substr(p, this.src.length);
              setTimeout(() => {
                this.src.setSelectionRange(p, p);
              }, 0);
            }
          }
        },
        change: function () {
          let l = this.code.substr(0, this.src.selectionStart).split("\n");
          this.focusedLine = l.length - 1;
          this.scroll();
          this.$forceUpdate();
        },
        scroll: function () {
            let s = this.src.scrollTop,
                h = this.src.scrollLeft;
            this.$refs.overlay.scrollTop = s;
            this.$refs.overlay.scrollLeft = h;
            this.isHaveScrollingV = s + this.$refs.overlay.clientHeight < this.src.scrollHeight -
                20;
            this.isHaveScrollingH = h > 10;
        },
        focus() {
            this.focused = true
        },
        blur() {
            this.focused = false
        }
    }
}
</script>



