<template>
    <div id="html-editor" class="editor" :class="{ 'scroll-v': isHaveScrollingV && focused, 'scroll-h': isHaveScrollingH && focused }" data-lang="html">
        <textarea class="src" @focus="focus" @blur="blur" ref="src" v-model="code" spellcheck="false" @keyup="keyup" @scroll="scroll"></textarea>
        <div class="overlay" ref="overlay">
            <div v-for="(line, i) in lines" :key="i" v-html="format(line)" :class="{ focus: i == focusedLine, tl: tabs[i] < tabs[i+1], tg: tabs[i-1] > tabs[i], td: tabs[i] == tabs[i+1], tu: tabs[i] == tabs[i-1] }"></div>
        </div>
    </div>
</template>

<script>
import {html} from '../data/lang'
const   tags = html.tags,
        attrs = html.attrs

export default {
    name: 'html-editor',
    data () {
    return {
        code: "<טבלה>\n  <שורה>\n    <תא מחלקה=\"כחול\">היי</תא><תא>שלום</תא>\n  </שורה>\n  <שורה מזהה=\"שור\">\n    <תא>\n        <קישור אל=\"http://google.com\">\n        מעבר לגוגל\n      </קישור>\n    </תא>\n    <תא><שדה סוג=\"מייל\"></שדה></תא>\n  </שורה>\n</טבלה>",
        focusedLine: 0,
        src: null,
        tagsReg1: null,
        tagsReg2: null,
        rawReg: null,
        isHaveScrollingH: false,
        isHaveScrollingV: false,
        focused: false
    }
    },
    mounted: function () {
        this.tagsReg1 = new RegExp('&lt;(/?)(' + Object.keys(tags).join("|") +
            ')&gt;', "g");
        this.tagsReg2 = new RegExp('&lt;(' + Object.keys(tags).join("|") +
            ')((<e> +</e>[^ ]+(="[^">]+"))+)&gt;', "g");
        this.rawReg = new RegExp('<(/?)(' + Object.keys(tags).join("|") +
            ')(( +[^ ]+(="[^">]+"))*)>', "g");
        this.src = this.$refs.src;
        this.src.onkeyup = this.change;
        this.src.onmouseup = this.change;
    },
    computed: {
        lines: function () {
            return this.code.split('\n');
        },
        tabs: function () {
            return this.lines.map((l) => l.length - l.trimLeft().length);
        }
    },
    methods: {
        keyup: function (e) {
            if (e.keyCode !== 13 && e.keyCode != 222 && e.keyCode != 8) return;
            let p = this.src.selectionStart;
            let l = this.code.substr(0, p);
            if (e.keyCode == 13) {
                this.focusedLine = l.split("\n").length - 1;
                if (this.tabs[this.focusedLine] !== 0) return;
                let t = this.tabs[this.focusedLine - 1],
                    s = '';
                for (let i = 0; i < t; i++)
                    s += ' ';
                this.code = l + s + this.code.substr(p, this.src.length);
                setTimeout(() => {
                    this.src.setSelectionRange(p + t, p + t);
                }, 0);
            } else if (e.keyCode == 8) {
                let l = this.code.substr(0, p + 1);
                if (l[l.length - 1] != '"' && l[l.length - 2] != '"') return;
                if (l[l.length - 2] == '"' && l[l.length - 1] == '"') {
                    this.code = l.substr(0, l.length - 2) + this.code.substr(p, this.src.length);
                    p--;
                } else
                    this.code = l.substr(0, l.length - 1) + this.code.substr(p + 1, this.src.length);
                setTimeout(() => {
                    this.src.setSelectionRange(p, p);
                }, 0);
            } else {
                this.focusedLine = l.split("\n").length - 1;
                if (l.split('"').length % 2 == 1) return;
                let cl = this.lines[this.focusedLine];
                if (cl.substr(p, cl.length).split('"').length % 2 == 1) {
                    this.code = l + '"' + this.code.substr(p, this.src.length);
                    setTimeout(() => {
                        this.src.setSelectionRange(p, p);
                    }, 0);
                }
            }
        },
        change: function () {
            let l = this.code.substr(0, this.src.selectionStart).split("\n");
            this.focusedLine = l.length - 1;
            this.scroll();
            this.$forceUpdate();
        },
        format: function (line) {
            let r = line
                .replace(/</g, '&lt;')
                .replace(/>/g, '&gt;')
                .replace(/^(\s*)/g, '<u>$&</u>')
                .replace(/(\s+)(?!<\/u>)/g, '<e>$1</e>')
                .replace(this.tagsReg1, '<b><i>&lt;$1</i>$2<i>&gt;</i></b>')
                .replace(this.tagsReg2, '<b><i>&lt;</i>$1<b>$2</b><i>&gt;</i></b>')
                .replace(/([^ ]+)(=)("[^"]*")/g, '$1<i>$2</i><s>$3</s>');
            return r;
        },
        scroll: function () {
            let s = this.src.scrollTop,
                h = this.src.scrollLeft;
            this.$refs.overlay.scrollTop = s;
            this.$refs.overlay.scrollLeft = h;
            this.isHaveScrollingV = s + this.$refs.overlay.clientHeight < this.src.scrollHeight -
                20;
            this.isHaveScrollingH = h > 10;
        },
        raw: function () {
            let o = '',
                lns = this.lines;
            for (let i = 0; i < lns.length; i++) {
                let l = lns[i]
                    .replace(this.rawReg, (f, c, t, a) => {
                        t = tags[t];
                        if (a.trim() == '')
                            return '<' + c + t + '>';
                        let ats = a.split(/\s+/g),
                            at = '';
                        for (let i = 0; i < ats.length; i++) {
                            let an = ats[i].split("=");
                            if(an.length < 2) continue;
                            let res = '';
                            if (attrs[t] !== undefined)
                                if (attrs[t][an[0]] !== undefined) 
                                    if (typeof attrs[t][an[0]] == "object") {
                                        if (an[1].charAt(0) == '"')
                                            an[1] = an[1].substr(1)
                                        if (an[1].slice(-1) == '"')
                                            an[1] = an[1].slice(0, -1)
                                        res = ` ${attrs[t][an[0]][0]}=${attrs[t][an[0]][1][an[1]]}`;
                                    }
                                    else
                                        res = ` ${attrs[t][an[0]]}=${an[1]}`;
                            if (res == '')
                                if (attrs.all[an[0]] !== undefined)
                                    res = ` ${attrs.all[an[0]]}=${an[1]}`;
                            at += res;
                        }
                        return '<' + t + at + '>';
                    })
                o += l;
            }
            return o;
        },
        focus() {
            this.focused = true
        },
        blur() {
            this.focused = false
        }
    }
}
</script>



