<template>
    <div id="kiddie" :class="`mode${mode} s${size}`">
        <div id="buttons">
            <div v-if="false" id="back-home" @click="back">חזרה</div>
            <div id="save-button">שמירה</div>
            <div id="run-button" @click="run">הרצה</div>
            <div id="mode-button" @click="changeMode">שנה פריסה</div>
            <div @click="incSize">+</div>
            <div @click="decSize">-</div>
        </div>
        <div id="tabs" v-if="mode > 0">
            <div :class="{selected: file == 0}" @click="file = 0">מבנה</div>
            <div :class="{selected: file == 1}" @click="file = 1">עיצוב</div>
            <div :class="{selected: file == 2}" @click="file = 2">סקריפט</div>
        </div>
        <html-editor 
            ref="htmlEditor"
            class="htmlEditor"
            :style="{'z-index': (file == 0)*1+1}"></html-editor> 
        <css-editor
            ref="cssEditor"
            class="cssEditor"
            :style="{'z-index': (file == 1)*1+1}"></css-editor> 
        <js-editor
            ref="jsEditor"
            class="jsEditor"
            :style="{'z-index': (file == 2)*1+1}"></js-editor> 
        <div id="result" v-html="resCode">
        </div>
    </div>
</template>
<style lang="scss">
@import './assets/_index';
</style>
<script>
import htmlEditor from './components/html.vue'
import cssEditor from './components/css.vue'
import jsEditor from './components/js.vue'

export default {
    name: 'App',
    data() {
        return {
            resCode: '',
            html: null,
            css: null,
            js: null,
            mode: 0,
            size: 2,
            file: 0
        }
    },
    mounted(){
        this.html = this.$refs.htmlEditor;
        this.css = this.$refs.cssEditor;
        this.js = this.$refs.jsEditor;
    },
    components: {
        'html-editor': htmlEditor,
        'css-editor': cssEditor,
        'js-editor': jsEditor
    },
    methods: {
        run: function () {
            this.js.raw();
            this.resCode = `${this.html.raw()}<style>${this.css.raw()}</style>`;
            try {
                eval(this.js.raw());
            } catch (e) {
                console.log(e);
                console.log(this.js.raw());
                
            }
        },
        back: function () {
            this.$root.page('/')
        },
        changeMode: function () {
            this.mode = (this.mode + 1) % 3 
        },
        incSize: function () {
            this.size = Math.min(this.size + 1, 4)
        },
        decSize: function () {
            this.size = Math.max(this.size - 1, 0)
        }
    }

}
</script>



